import errorMsg from '@/components/errorMsg/errorMsg';
export default {
	components: {
		errorMsg
	},
	props: {
		goodsPrice: {
			type: Array
		},
		current: {//采购，销售上下架按钮状态
			type: Number
		},
		entrust:{//订单委托
			type: Boolean,
			default: false
		},
		priceHide: {//报价删除按钮hide
			type: Boolean,
			default: true
		},
		priceTime: {//报价显示时间
			type: Boolean,
			default: false
		},
		loadStatus: {
			type: String,
			default: '正在加载....'
		}
	},
	data() {
		return {};
	},
	methods: {
		gotoDetails(item) {
			console.log(item);
			this.$emit('details-url', item);
			// console.log(item,625)
		},
		handleDelete(item) {
			this.$emit('update-value', item);
		},
		handleUp(item) {
			this.$emit('update-up', item);
		},
		handleDown(item) {
			this.$emit('update-down', item);
		},
		handlemodify(item) {
			this.$emit('update-modify', item);
		}
	},
	onLoad() {}
};
